.terms .wp-block-group {
  display: flex;
  flex-direction: column;
}

.terms .wp-block-group:not(:last-of-type) {
  margin-bottom: 50px;
}

@media (min-width: 1190px) {
  .terms .wp-block-group {
    min-width: 49.3%;
    margin-right: 20px;
    text-align: start;
  }
}

.terms .wp-block-heading {
  margin-bottom: 20px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: 1190px) {
  .terms .wp-block-heading {
    margin-bottom: 40px;
    font-size: 40px;
    line-height: 48px;
    text-align: start;
  }
}

.terms .wp-block-group>p {
  padding: 15px 25px;
  font-size: 16px;
  line-height: 16px;
  border-radius: 30px;
  background-color: var(--main-bg-color-additional);
}

.terms .wp-block-group>p:not(:last-of-type) {
  margin-bottom: 20px;
}

@media (min-width: 1190px) {
  .terms .wp-block-group>p {
    padding: 20px 50px;
    font-size: 16px;
    line-height: 20px;
    border-radius: 45px;
  }
}

.team__list>.wp-block-group {
  align-items: center !important;
  justify-content: center !important;
  min-height: 560px;
}

@media (min-width: 1190px) {
  .team__list>.wp-block-group {
    min-height: auto;
  }
}

.team__list>.wp-block-group img {
  width: auto;
  height: auto;
}

.team__list>.wp-block-group p:first-of-type {
  padding: 10px 10px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 28px;
}

@media (min-width: 1190px) {
  .team__list>.wp-block-group p:first-of-type {
    padding: 0 7px;
    margin-top: 15px;
    font-size: 24px;
    line-height: 36px;
  }
}


.team__list>.wp-block-group p:last-of-type {
  font-size: 16px;
  line-height: 21px;
  text-indent: 0;
  text-align: center;
  font-style: normal;
}

@media (min-width: 1190px) {
  .team__list>.wp-block-group p:last-of-type {
    min-width: 304px;
    padding: 0 7px;
    margin-top: 5px;
    font-size: 18px;
  }
}

.team__list>.wp-block-group.carousel__slide {
  display: none;
  flex-shrink: 0;
  width: 100%;
  margin-bottom: auto;
}

.team__list>.wp-block-group.carousel__slide--mobile {
  display: flex;
}

@media (min-width: 1190px) {
  .team__list>.wp-block-group.carousel__slide {
    z-index: 1;
  }

  .team__list>.wp-block-group.carousel__slide--desktop {
    display: flex;
    flex-direction: column;
    width: min-content;
  }
}

/* Price page */
.price>.wp-block-heading {
  padding-top: 20px;
  padding-bottom: 20px;
}

.price .wp-block-buttons {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  width: calc(100% - 30px);
  margin: 0 auto;
  margin-bottom: 26px;
  margin-top: 20px;
}

.price .wp-block-button {
  font-family: "Roboto Cond", sans-serif;
  padding: 10px 10px;
  font-size: 20px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  color: $font-color;
  text-decoration: none;
  margin-bottom: 13px;
  cursor: pointer;
}

.examples__link--active {
  color: #ffffff !important;
  background-color: #00659e !important;
}

.price .wp-block-button:not(:last-of-type) {
  margin-right: 20px;
}


.price .has-fixed-layout {
  width: 100%;
  table-layout: fixed;
  overflow-x: auto;
  /* display: none; */
  border-collapse: collapse;
}

.price .wp-block-table {
  display: none;
}

.price .examples__tab--active {
  display: block;
  overflow-x: auto;
  width: 100%;
}

.price .wp-block-button__link.wp-element-button {
  padding: 0;
}

.price .has-fixed-layout tr {
  border-bottom: 2px solid #acacac;
}

.price .has-fixed-layout td,
th {
  padding: 10px;
  text-align: center;
  font-size: 12px;
}

.price .has-fixed-layout td:nth-of-type(1),
.price .has-fixed-layout th:nth-of-type(1) {
  text-align: left;
  width: 60%;
}

.price .has-fixed-layout td {
  font-size: 14px;
}

.price .wp-block-button__link {
  border: none !important;
  line-height: 23px !important;
}

@media (min-width: 768px) {

  .price .has-fixed-layout td,
  th {
    font-size: 16px;
  }

  .price .has-fixed-layout td {
    font-size: 18px;
  }

  .price .has-fixed-layout td:nth-of-type(1),
  .price .has-fixed-layout th:nth-of-type(1) {
    text-align: left;
    width: 70%;
  }

  /* .price > .wp-block-buttons, */
  .price .wp-block-table,
  .price .wp-container-2 {
    width: calc(100% - 40px);
    max-width: 750px;
    margin: 0 auto;
  }

  .price .wp-block-button {
    min-width: fit-content;
    padding: 12px 24px;
  }
}

@media (min-width: 1190px) {

  /* .price > .wp-block-buttons, */
  .price .wp-block-table,
  .price .wp-container-2 {
    width: calc(100% - 50px);
    max-width: 1490px;
    margin: 0 auto;
  }

  .price .wp-block-buttons {
    align-items: center;
    width: fit-content;
    max-width: none;
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .price .wp-block-button {
    width: fit-content;
    padding: 15px 35px;
    margin-bottom: 0;
  }

  .price .wp-block-button:not(:last-of-type) {
    margin: 0;
    margin-right: 20px;
  }

  .price .examples__tab--active {
    max-width: 100%;
  }
}

@media (min-width: 1550px) {

  /* .price > .wp-block-buttons, */
  .price .wp-block-table,
  .price .wp-container-2 {
    width: 100%;
    max-width: 1490px;
    margin: 0 auto;
    margin-bottom: 20px;
    margin-top: 20px;
  }
}

.wp-block-heading.page__title {
  max-width: initial;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 20px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: 1190px) {
  .wp-block-heading.page__title {
    margin: 0;
    margin-top: 60px;
    margin-bottom: 45px;
    font-size: 40px;
    line-height: 48px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.wp-post-thumbnail.list__link-cover.examples__item-img img {
  width: 100%;
  height: auto;
  object-fit: cover;
}
